/* React */
import React, { useState } from 'react';

/* GraphQL */
import client from '@graphql/client';
import { useQuery } from '@apollo/react-hooks';
import { LOTTO_PRZWIN_QUERY } from '@graphql/query/lotto';

/* Material-UI */
import Paper from '@material-ui/core/Paper';

/* Custom Component */
import { SelectedToolBar } from '@components/ToolBar';
import { CheckableTable } from '@components/Table';

/* Main Component */
const Lotto = props =>{
  /* Props */
  const {
    className,
    ...rest
  } = props;

  /* State */
  const [variables, setVariables] = useState({});
  const [selected, setSelected] = useState([]);

  /* GraphQL */
  const { loading, error, data, refetch } = useQuery(
    LOTTO_PRZWIN_QUERY, { 
      client,
      variables,
      onError( e ){
        console.log( "onError", e );
      },
      onCompleted( c ){
        console.log( "onCompleted", c );
      },
    }
  );

  /* Rendering */
  return (
    <Paper>
      <SelectedToolBar
        title=""
        selected={ selected }
      />
      <CheckableTable
        dataKey="drwtNo"
        checkbox={ false }
        columns={[
          { id: "drwtNo", label: "", align: "center", minWidth: 30 },
          { id: "drwtNo1", label: "1", align: "center", minWidth: 30 },
          { id: "drwtNo2", label: "2", align: "center", minWidth: 30 },
          { id: "drwtNo3", label: "3", align: "center", minWidth: 30 },
          { id: "drwtNo4", label: "4", align: "center", minWidth: 30 },
          { id: "drwtNo5", label: "5", align: "center", minWidth: 30 },
          { id: "drwtNo6", label: "6", align: "center", minWidth: 30 },
          { id: "drwtNoBnus", label: "", align: "center", minWidth: 30 },
          { id: "drwtNoDate", label: "", align: "center", type: "date", format: "YYYY-MM-DD", minWidth: 30 }
        ]}
        rows={ loading ? null : data.przwinList }
        loading={ loading }
        error={ error }
        selected={ selected }
        onSelected={ setSelected }
      />
    </Paper>
  );
}

export default Lotto;